@import url(https://fonts.googleapis.com/css?family=Amatic+SC);
@import url(https://fonts.googleapis.com/css?family=Hind+Vadodara);
body {
  margin: 0;
  padding: 100px 0 0 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  outline: 0;
}

@media only screen and (max-width: 768px) {
  body {
    padding: 10px 0 0 0;
  }
}
.App {
  font-family: 'Hind Vadodara', sans-serif;
  font-weight: 400;
}

.content {
  float: left;
  width: calc(100% - 250px);
  margin-left: 250px;
}

@media only screen and (max-width: 768px) {
  .content {
    width: 100%;
    margin-left: 0px;
  }
}





.header-item {
  font-family: 'Hind Vadodara', sans-serif;
  font-size: 16px;
  font-weight: 400;
  text-transform: capitalize;
  position: relative;
  top: 50%;
}

.header-logo {
  padding-right: 47px;
}

.header-item a, a:hover {
  color: #424242;
  text-decoration: none;
}


@media only screen and (min-width: 768px) {
  .header {
    display: none;
  }
}

@media only screen and (max-width: 520px) {
  .header-logo {
    left: 35%;
  }
}

.sidebar {
  position: fixed;
  width: 250px;
}

.logo img {
  image-rendering: crisp-edges;
}

.sidebar-item {
  font-family: 'Hind Vadodara', sans-serif;
  font-size: 15px;
  font-weight: 400;
  position: relative;
  top: 50%;
}

.sidebar-item a, a:hover {
  color: #424242;
  text-decoration: none;
}

.copyright {
  position: fixed;
  width: 250px;
  bottom: 0;
}

@media only screen and (max-width: 768px) {
  .sidebar {
    display: none !important;
  }
}
.image-title {
  font-family: 'Hind Vadodara', sans-serif;
  font-size: 18px;
  font-weight: 500;
  text-transform: capitalize; 
  color: #333;
}

.grid-image {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.art-image {
  opacity: 1;
  -webkit-transition: opacity .4s ease;
  transition: opacity .4s ease;
  max-width: 600px;
  max-height: 600px;
  
}

.grid-image:hover .art-image {
  opacity: 0.75;
  -webkit-transition: opacity .5s ease;
  transition: opacity .5s ease;

}

p.image-title {
  opacity: 0;
  -webkit-transition: opacity 0.4s;
  transition: opacity 0.4s;
}

.grid-image:hover a p.image-title {
  opacity: 1;
  -webkit-transition: opacity 0.4s;
  transition: opacity 0.4s;
}

#post-250 {
  width: 500px;
}

@media only screen and (max-width: 1600px) {
  .art-image {
    max-width: 400px;
    max-height: 400px;
  }
}

@media only screen and (max-width: 1400px) {
  .art-image {
    max-width: 300px;
    max-height: 300px;
  }
}

@media only screen and (max-width: 1200px) {
  .art-image {
    max-width: 300px;
    max-height: 300px;
  }
}

@media only screen and (max-width: 992px) {
  .art-image {
    max-width: 400px;
    max-height: 400px;
  }
}

@media only screen and (max-width: 480px) {
  .art-image {
    max-width: 300px;
    max-height: 300px;
  }

  p.image-title {
    opacity: 1 !important;
  }
}

.contact{
  text-align: left;
}

.contact h3.info-label {
  font-family: 'Hind Vadodara', sans-serif;
  font-size: 16px;
  font-weight: 600;
  color: black;
  text-transform: uppercase;
}

.contact p {
  font-family: 'Hind Vadodara', sans-serif;
  color: #333;
}



.fa {
  position: relative;
  padding: 20px 20px;  
  text-align: center;
}

.fa-instagram{
  color: #810000;
}

.fa-leaf {
  color: #004225;
}

.fa-behance {
  color: #002366;
}
.artwork-container img {
  max-width: 400px;
  max-height: 400px;
}

.description p {
  margin-bottom: 10px;
}

.info-wrapper a {
  color: #227093;
}

.info-wrapper a:hover {
  color: #333;
}

.block-image {
  display: block;
}


.description-text {
  opacity: 1;
  background-color: #fff;
  -webkit-transition: opacity 1s ease;
  transition: opacity 1s ease;
}

.description-text-hidden {
  opacity: 0;
  -webkit-transition: opacity 1s ease;
  transition: opacity 1s ease;
}

